<template>
  <div>
  
    <el-row>
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>
          <a href="/">余额管理</a>
        </el-breadcrumb-item>
      </el-breadcrumb>
    </el-row>
    <el-row  class="mt15" :gutter="10">
      <el-col :span="8">
        <el-card style="min-height:200px">
          <h3>余额 ：{{ wallet.can_money}} - 冻结 ： {{ wallet.freeze_money }}</h3>
          <el-button class="mt15" @click="$router.push({ path : '/admin/enterprise/wallet_up'})">充值</el-button>
        </el-card>
      </el-col>
      <el-col :span="8">
        <el-card style="min-height:200px; color:#f60">
          <p>
            注意：冻结金额为创建任务但是无人领取任务，暂放开台的金额。任务结束后会返回
          </p>
          <p>
            注意：系统充值金额可能会有延迟，请耐心等待
          </p>
        </el-card>
      </el-col>
    </el-row>
    <el-row class="mt15" :gutter="5">
      
    </el-row>
    <el-row class="mt15" :gutter="10">
      <el-col :span="6" :sm="6" :xs="8">
        <el-date-picker
          v-model="date_time"
          type="daterange"
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
          format="yyyy-MM-dd HH:mm:ss"
          value-format="yyyy-MM-dd HH:mm:ss"
        ></el-date-picker>
      </el-col>

      <el-col :span="2">
        <el-button icon="el-icon-search" @click="searchBtn">查询</el-button>
      </el-col>
      <el-col :xl="1" :lg="1" :md="3" :sm="4" :xs="4">
        <el-button type="success" @click="reload">刷新</el-button>
      </el-col>
    </el-row>
    <el-table class="mt15" :data="tableData" border style v-loading="loading">
      <el-table-column type="index" label="编号" fixed width="60" align="center" header-align="center"></el-table-column>
      <el-table-column fixed prop="now_money" label="操作后金额" align="center" header-align="center"></el-table-column>
      <el-table-column fixed prop="money" label="操作金额" align="center" header-align="center"></el-table-column>
      <el-table-column fixed prop="pre_money" label="操作时金额" align="center" header-align="center"></el-table-column>
      <el-table-column fixed prop="message" label="操作内容" align="center" header-align="center"></el-table-column>
       <el-table-column prop="created_at" label="记录时间" align="center" header-align="center">
      </el-table-column>
    </el-table>
    <div class="mt15">
      <el-pagination layout="prev, pager, next" :total="total" :page-size="limit" @current-change="pageChange" :background="true"></el-pagination>
    </div>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      wallet:{},
      search : {
      },
      date_time: "",
      value: 0,
      options:[1],
      total: 1000,
      limit: 10,
      page:1,
      loading:true,
      tableData:[],
    };
  },
  created(){
     this.loadData({});
  },
  mounted() {
    this.loading = false
  },
  methods: {
    loadData (search) {
      console.log(search)
      let sea = search ? search : {};
      sea.page=this.page;
      sea.limit=this.limit
      this.$api.get('enterprise/get_wallet',sea, res=>{
        this.wallet=res.data.data;
        this.tableData=res.data.data.log.list
        this.total = res.data.data.log.total
      });
    },
    dateRes (date) {
        var d = new Date(date);  
        var  resDate = d.getFullYear() + '-' + (d.getMonth() + 1) + '-' + d.getDate() + ' ' + d.getHours() + ':' + d.getMinutes() + ':' + d.getSeconds(); 
        return resDate;
    },
    searchBtn(){
      var data = {
        begin_time:this.date_time[0],
        end_time:this.date_time[1],
      };
      this.search=data;
      this.loadData(data)
    },
    handleClick(row) {
      console.log(row);
    },
    del(id){
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$api.post('/enterprise/del_contract', {id : id}, res=> {
            if(res.data.bol)
            {
              this.$message.success(res.data.msg);
              this.loadData({});
            }else{
              this.$message.error(res.data.msg);
            }
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
    },
    pageChange(e)
    {
      this.page=e;
      this.loadData({});
    },
    reload()
    {
      window.localtion.reload();
    }
  }
};
</script>

<style>
  
</style>



